@font-face
    font-family: 'Murmure'
    src: url('fonts/Le_Murmure-Regular_web.woff') format('woff'), url('fonts/Le_Murmure-Regular_web.woff2') format('woff2')
    font-weight: normal
    font-style: normal

@font-face
    font-family: 'HKGrotesk'
    src: url('fonts/HKGrotesk-Regular.otf') format('opentype')
    font-weight: normal
    font-style: normal
@font-face
    font-family: 'HKGrotesk'
    src: url('fonts/HKGrotesk-Bold.otf') format('opentype')
    font-weight: bold
    font-style: normal

body
    background-color: blue
    padding-top: 20vh
    overflow-x: hidden 

*
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

.background
    position: absolute
    top: 0
    z-index: -1
    mix-blend-mode: lighten
    img
        position: absolute
        max-width: 50vw

.container
    max-width: 620px
    width: 100%
    margin: 0 auto

p
    display: inline
    background-color: red
    color: white
    font-size: 1.2rem
    font-family: 'HKGrotesk'
    font-weight: bold
    line-height: 1.5rem
    @media screen and (max-width: 25rem)
        font-size: 1rem
        line-height: 1rem
br
    line-height: 1rem


h1,h2,h3,h4,h5,h6,li
    font-size: 3rem
    display: inline-block
    background-color: red
    color: white
    font-family: 'Murmure'

h1
    font-size: 7rem

*::selection
    background-color: blue
    color: red

ul
    padding-left: 0
    
li
    font-size: 1rem
    font-family: 'HKGrotesk'
    font-weight: bold
    text-decoration: none
    @media screen and (max-width: 25rem)
        font-size: 1rem
        line-height: 1rem

img
    filter: contrast(2) saturate(6)